@import '~antd/dist/antd.css';

// variables

// ant gray scale
$white0: #ffffff;
$white1: #fafafa;
$white2: #f5f5f5;
$white3: #f0f0f0;
$black0: #d9d9d9;
$black1: #bfbfbf;
$black2: #8c8c8c;
$black3: #595959;
$black4: #434343;
$black5: #262626;
$black6: #1f1f1f;
$black7: #141414;
$black8: #000000;

// tri chromatic color scheme
$color0: #1890ff;
$color1: #1abc9c;
$color2: #f27935;

$left: 20px;

@mixin base {

  background-color: $white0;
  color: $black8;
  
}

@mixin horizontal-menu {
  
  ul {
      
      padding-top: 10px;
      
  }

  li {

      // make it a horizontal list
      display: inline; 
      padding-right: 50px;

  }
}

body {

  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  @include base;

}

@mixin heading {

  color: $color0;
  
}

h1 {

  @include heading;
}

h2 {

  @include heading;

}

h3 {

  @include heading;

}

h4 {

  @include heading;


}

code {
  color: $color1;
}

.clickable {

  cursor: pointer;
  
}